/**
 * Univers
 */

@font-face {
  font-family: 'univers-cb';
  src: url('../fonts/univers-cb.eot');
  src: url('../fonts/univers-cb.eot?#iefix') format('embedded-opentype'),
     url('../fonts/univers-cb.woff') format('woff'),
     url('../fonts/univers-cb.ttf') format('truetype'),
     url('../fonts/univers-cb.svg#univers-cb') format('svg');
  font-weight: normal;
  font-style: normal;
}